























import { Vue, Component, Prop } from 'vue-property-decorator';
import User from '@/models/User';

@Component({
    name: 'UserListItem'
})
export default class UserListItem extends Vue {
    @Prop()
    public user!: User;
    get userImg(): string {
        if (!this.user) {
            return '';
        }
        return `img/${this.user.user_id}.jpg`;
    }
}
